html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

.m1-counter {
  border: 2px solid #ec005a;
  border-radius: 3px;
  align-items: center;
  margin: 16px 0;
  padding: 15px;
  display: flex;
}

.m1-counter__value {
  color: #fff;
  text-align: center;
  width: 70px;
  background-color: #ec005a;
  border-radius: 3px;
  margin-right: 15px;
  padding: 10px 0;
  font-size: 36px;
}

.m1-counter__form {
  margin: 0 0 6px;
}

.m1-counter__timestamp {
  color: #999;
}

